//main colors
$primary_blue: #0162d0;
$primary_blue_dark: #0129b5;
$primary_light: #f0faff;
$primary_dark: #121417;


//main fonts
@mixin primary_font-p {
  font-family: Ubuntu, sans-serif;
  font-size: 14px;
  font-weight: 600; }

@mixin primary_font-h1 {
  font-family: Ubuntu, sans-serif;
  font-size: 25px;
  font-weight: 800; }

@mixin primary_font-menu {
  font-family: Ubuntu, sans-serif;
  font-size: 16px;
  font-weight: 600; }

//flex
@mixin flex-all-center {
  display: flex;
  justify-content: center;
  align-items: center; }

@mixin flex-center-sb {
  display: flex;
  justify-content: space-between;
  align-items: center; }

@mixin flex-center-sa {
  display: flex;
  justify-content: space-around;
  align-items: center; }

@mixin flex-columns {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center; }


@mixin block-shadow {
  box-shadow: 1px 1px 4px 1px rgba(0,0,0,0.33);
  -webkit-box-shadow: 1px 1px 4px 1px rgba(0,0,0,0.33);
  -moz-box-shadow: 1px 1px 4px 1px rgba(0,0,0,0.33); }

.buttons_block {
  width: 70%;
  @include flex-center-sb; }

.buttons_block-bottom {
  width: 40%;
  @include flex-center-sb; }

.white-btn {
  width: 45%;
  min-height: 40px;
  padding: 5px;
  color: $primary_blue;
  background-color: $primary_light;
  border: none;
  border-radius: 3px;
  outline: none;
  cursor: pointer;
  font-size: 14px;
  &:hover {
    text-decoration: underline $primary_blue; } }

.white-btn-border {
  width: calc(45% - 2px);
  min-height: calc(40px - 2px);
  padding: 5px;
  color: $primary_blue;
  background-color: $primary_light;
  border-radius: 3px;
  outline: none;
  cursor: pointer;
  font-size: 14px;
  border: 1px solid $primary_blue;
  &:hover {
    text-decoration: underline $primary_blue; } }

.blue-btn {
  width: 45%;
  min-height: 40px;
  padding: 5px;
  color:  $primary_light;
  background-color: $primary_blue_dark;
  border: none;
  border-radius: 3px;
  outline: none;
  cursor: pointer;
  font-size: 14px;
  &:hover {
    text-decoration: underline $primary_light; } }


