@import "src/assets/styles/colors_fonts";

.homePage__top-container {
  @include flex-center-sb;
  width: 100%;
  background-color: $primary_blue;
  height: 500px; }

.homePage__top-block {
  @include flex-center-sa;
  max-width: 1400px;
  width: 100%;
  margin: 0 auto; }

.homePage__top-left {
  @include flex-columns;
  align-items: flex-start;
  color: $primary_light;
  width: 63%;
  h1 {
    max-width: 590px;
    font-size: 51px !important; }
  h3 {
    max-width: 510px;
    font-size: 28px !important; } }

.homePage__top-img {
  width: 80%;
  height: 45%;
  margin-left: 30px; }

.homePage__top-right {
  display: flex;
  justify-content: flex-end; }

.homePage__codeTabs-container {
  margin-top: 70px;
  color: black;
  display: flex;
  justify-content: space-between;
  width: 40%;
  div {
    cursor: pointer; } }


//code preview [Highlight] change styles
.homePage__top-left {
  .pgsql {
    padding: 60px !important;
    width: 600px;
    height: 200px;
    overflow-y: auto;
    word-break: break-word; } }

.homePage__right-description {
  padding-left: 50px; }

.homePage__middle-right {
  display: flex;
  justify-content: flex-start; }

.homePage__noActiveTab {
  background-color: $primary_blue;
  color: white;
  height: 35px;
  padding: 15px;
  @include flex-all-center;
  &:hover {
    text-decoration: underline white; } }

.homePage__activeTab {
  background-color: $primary_blue_dark;
  color: white;
  @extend .homePage__noActiveTab;
  &:hover {
    text-decoration: none; } }

.homePage__bottom-block {
  @extend .homePage__top-block;
  flex-direction: column;
  margin: 70px auto 140px auto; }
