@import "src/assets/styles/colors_fonts";


.header__container {
  background-color: $primary_blue;
  width: 100%;
  height: 100px; }

//Left side styles
.header__block {
  @include flex-center-sb;
  color: $primary_light;
  max-width: 1400px;
  margin: 0 auto;
  height: 100%;

  h1 {
    @include primary_font-h1; }

  .header__block-img {
    max-width: 55px;
    max-height: 55px;
    margin-right: 20px; } }

.header__block-left {
  @include flex-center-sb;
  max-width: 300px; }

//Navbar styles
.header__menu-container {
  @include flex-center-sb; }

.header__menu-title {
  @include primary_font-menu;
  @include flex-all-center;
  margin: 0 20px 0 0;
  cursor: pointer;
  padding: 10px 0;

  &:last-child {
    margin-left: 35px; }

  img {
    width: 10px;
    height: 10px;
    margin-left: 6px; } }


//Product`s block (from menu) styles
.products-dropdown {
  position: relative; }

.products-title {
  &:hover > .products__container {
    display: flex; } }

.products__container {
  @include block-shadow;
  @include flex-columns;
  color: $primary_dark;
  width: 80%;
  display: none;
  left: -50px;
  top: 35px;
  border-radius: 10px;
  padding: 20px;
  background-color: $primary_light;
  position: absolute;
  z-index: 10;
  cursor: default; }

.products__block {
  width: 100%;
  margin: 0 0 15px 0;
  border-bottom: 1px solid gray;

  &:last-child {
    border-bottom: none; } }

.products__block-line {
  @include flex-center-sa;
  width: 100%;
  margin: 25px 0; }

.products__block-cell {
  min-width: 33.2%;
  display: flex;
  align-items: center;
  font-size: 14px;
  font-weight: 400;
  cursor: pointer; }

.products__block-cell {
  a {
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none !important;
    color: black; } }

.two-item-block-cell {
  @extend .products__block-cell;
  max-width: 23.2%; }

.two-item-block-cell {
  a {
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none !important;
    color: black; } }

.flex-start {
  justify-content: flex-start; }

.menu-icon-img {
  width: 35px !important;
  height: 35px !important;
  margin-right: 5px; }

.header__block-left {
  a {
    text-decoration: none;
    display: flex;
    color: $primary_light; } }
